import React from 'react';
import SlickSlider from 'react-slick';
import styled from 'styled-components';

const Container = styled.ul`
    padding: 0;
    margin:0;
    .slick-prev,
    .slick-next {
        z-index: 50;
        top: 0;
        bottom: 0;
        margin: auto;
        width: 30px;
        height: 30px;
        transform: initial;
        &:before {
            font-size: 30px;
        }
    }

    .slick-prev {
        left: -36px;
        background-color: #000000;
        height: 210px;
    }

    .slick-next {
        display: block;
        background-color: #000000;
        height: 210px;
        width: 50px;
        right: 0px;
    }
`;

export const SliderItem = styled.li`
    margin-right: 16px;
    img {
        margin: 16px;
        width: 298px;
        height: 197px;
        object-fit: cover;
    }
`;

const Slider = ({ children }) => (
    <Container>
        <SlickSlider {...{
            dots: false,
            infinite: false,
            speed: 300,
            centerMode: false,
            variableWidth: true,
            adaptiveHeight: true,
        }}>
            {children}
        </SlickSlider>
    </Container>
);

export default Slider;